import React, { Fragment, useEffect, useState } from "react";
import { Link } from "gatsby";
import { Button, Card, Col, Container, Row } from "react-bootstrap";
import { utils } from "@core/helper";
import moment from "moment";
import { StaticImage } from "gatsby-plugin-image";
import Loading from "@components/common/loading";
import LoadMore from "@components/common/loadmore";

const BlogsData = (data) => {
  const { db } = data;
  let count = 0;
  const { numberItemsPagination, loadMoreItems, isLoading } = LoadMore(6);
  const [trendingBlogs, setTrendingBlogs] = useState([]);
  const [isMounted, setIsMounted] = useState(false);
  const [blogsList, setBlogsList] = useState([]);

  const loadBlogsList = async (db) => {
    try {
      // const collectionRef = collection(db, "blog")
      //   const querySnapshot = await getDocs(collectionRef);
      //   const data = querySnapshot.docs.map((doc) => ({
      //     id: doc.id, // Each document id
      //     ...doc.data(), // Spread the document data
      //   }));
      const data = await utils.getDocData(db, "blog");
      if (data?.length > 0) {
        setBlogsList(data);
      } else {
        setBlogsList([]);
      }
    } catch (err) {
      setBlogsList([]);
      console.log("err:;", err);
    }
  };

  useEffect(() => {
    if (isMounted && db) {
      loadBlogsList(db);
    }
  }, [isMounted, db]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  useEffect(() => {
    if (isMounted) {
      if (blogsList?.length > 0) {
        const sortTrendingBlogs = async (db, blogs) => {
          let addViewCountArr = [];
          let blogsCountsArr = await utils.getDocData(db, "viewCount");
          for (let blogObj of blogs) {
            for (let blogsCountDoc of blogsCountsArr) {
              if (blogObj?.slug === blogsCountDoc?.key) {
                blogObj.viewCount = blogsCountDoc?.count;
                addViewCountArr.push(blogObj);
              }
            }
            setTrendingBlogs(addViewCountArr);
          }
        };
        sortTrendingBlogs(db, blogsList);
      }
    }
  }, [isMounted, db, blogsList]);

  trendingBlogs.sort((a, b) => b.viewCount - a.viewCount);

  return (
    <Container fluid className="p-0">
      {trendingBlogs?.length > 0 && (
        <Fragment>
          <div className="blog_details_bg">
            <h1>Trending Blogs</h1>
          </div>
          <div className="blog_list_bg">
            <Container className="blog_container_position">
              <Row className="blog_row_gap">
                {trendingBlogs?.slice(0, 6).map((ele, ind) => {
                  let blogId = ind;
                  if (blogId % 2 === 0) {
                    count = count + 1;
                  }
                  return (
                    <Col lg={6} xs={12} className="p-lg-0">
                      {count % 2 !== 0 ? (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col sm={6} xs={12} className="p-sm-0">
                              <Link
                                to={`${ele?.slug}`}
                                className="text-decoration-none  d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  <StaticImage
                                    src={`../../styles/assets/images/no-image.jpg`}
                                    alt={ele?.image_alt}
                                    className="jt_blog_img"
                                  />
                                  <div className="blog_arrow_first">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                            <Col
                              sm={6}
                              xs={12}
                              className="p-sm-0 d-flex justify-content-center "
                            >
                              <Link
                                to={`${ele?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {moment(ele?.blog_created_date).format(
                                    "DD MMMM YYYY"
                                  )}
                                </p>

                                <p className="jt_blog_title">{ele?.title}</p>
                                <p className="jt_blog_description">
                                  {ele?.short_description.slice(0, 65) + "..."}
                                </p>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      ) : (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col sm={6} xs={12} className="p-sm-0 order_two">
                              <Link
                                to={`${ele?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {moment(ele?.blog_created_date).format(
                                    "DD MMMM YYYY"
                                  )}
                                </p>

                                <p className="jt_blog_title">{ele?.title}</p>
                                <p className="jt_blog_description">
                                  {ele?.short_description.slice(0, 65) + "..."}
                                </p>
                              </Link>
                            </Col>
                            <Col sm={6} xs={12} className="p-0 order_one">
                              <Link
                                to={`${ele?.slug}`}
                                className="text-decoration-none  d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  <StaticImage
                                    src={`../../styles/assets/images/no-image.jpg`}
                                    alt={ele?.image_alt}
                                    className="jt_blog_img"
                                  />

                                  <div className="blog_arrow_second">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      )}
                    </Col>
                  );
                })}
              </Row>
            </Container>
          </div>
        </Fragment>
      )}
      {blogsList?.length > 0 && (
        <Fragment>
          <div
            className="blog_details_bg"
            style={{
              background: "transparent",
            }}
          >
            <h2
              style={{
                color: "#7534fc",
              }}
            >
              Latest Blogs
            </h2>
          </div>
          <div
            className="blog_list_bg"
            style={{
              background: "transparent",
            }}
          >
            <Container className="blog_container_position">
              <Row className="blog_row_gap">
                {blogsList?.slice(0, numberItemsPagination).map((ele, ind) => {
                  let blogId = ind;
                  if (blogId % 2 === 0) {
                    count = count + 1;
                  }
                  let imagePath = `../../images/${ele?.image_path}.png`;

                  return (
                    <Col lg={6} xs={12} className="p-lg-0">
                      {count % 2 !== 0 ? (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col sm={6} xs={12} className="p-sm-0">
                              <Link
                                to={`${ele?.slug}`}
                                className="text-decoration-none d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  <StaticImage
                                    src={
                                      "../../styles/assets/images/no-image.jpg"
                                    }
                                    alt={ele?.image_alt}
                                    className="jt_blog_img"
                                  />
                                  <div className="blog_arrow_first">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M0 9L15 0.339745L15 17.6603L0 9Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                            <Col sm={6} xs={12} className="p-sm-0">
                              <Link
                                to={`${ele?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {moment(ele?.blog_created_date).format(
                                    "DD MMMM YYYY"
                                  )}
                                </p>
                                <p className="jt_blog_title">{ele?.title}</p>
                                <p className="jt_blog_description">
                                  {ele?.short_description.slice(0, 65) + "..."}
                                </p>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      ) : (
                        <Card key={ele.id} className="jt_blog_card">
                          <Row className="mx-lg-0">
                            <Col sm={6} xs={12} className="p-sm-0 order_two">
                              <Link
                                to={`${ele?.slug}`}
                                className="jt_blog_card_body"
                              >
                                <p className="jt_blog_date">
                                  {moment(ele?.blog_created_date).format(
                                    "DD MMMM YYYY"
                                  )}
                                </p>

                                <p className="jt_blog_title">{ele?.title}</p>
                                <p className="jt_blog_description">
                                  {ele?.short_description.slice(0, 65) + "..."}
                                </p>
                              </Link>
                            </Col>
                            <Col sm={6} xs={12} className="p-0 order_one">
                              <Link
                                to={`${ele?.slug}`}
                                className="text-decoration-none  d-block "
                              >
                                <div
                                  id="arrow_right"
                                  className="jt_blog_img_wrapper"
                                >
                                  {/* <Image
                                      src={siteUrl + blog?.node?.blogImg}
                                      alt={ele?.image_alt}
                                      className="jt_blog_img"
                                    /> */}

                                  <StaticImage
                                    src={`../../styles/assets/images/no-image.jpg`}
                                    alt={ele?.image_alt}
                                    className="jt_blog_img"
                                  />

                                  <div className="blog_arrow_second">
                                    <svg
                                      xmlns="http://www.w3.org/2000/svg"
                                      width="15"
                                      height="18"
                                      viewBox="0 0 15 18"
                                      fill="none"
                                    >
                                      <path
                                        d="M15 9.22363L0 17.8839L0 0.563378L15 9.22363Z"
                                        fill="white"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </Link>
                            </Col>
                          </Row>
                        </Card>
                      )}
                    </Col>
                  );
                })}

                {numberItemsPagination !== blogsList?.length && (
                  <Col md={12}>
                    <Button
                      disabled={
                        isLoading || numberItemsPagination >= blogsList?.length
                      }
                      className="blog_load_btn"
                      onClick={() => loadMoreItems(numberItemsPagination + 2)}
                    >
                      Load more {isLoading && <Loading />}
                    </Button>
                  </Col>
                )}
              </Row>
            </Container>
          </div>
        </Fragment>
      )}
    </Container>
  );
};

export default BlogsData;
